import React, { Component } from 'react';
import SuperSelect from 'react-super-select';
import './react-super-select.css';
import Site from './site';
import './App.css';

var categories = ['date', 'people', 'software', 'gps', 'redaction'];

class App extends Component {
  constructor(props){
    super(props);
    this.state = {};
            this.doShowHelp = this.doShowHelp.bind(this);
        this.doHideHelp = this.doHideHelp.bind(this);
  }



    doShowHelp() {
        this.setState({
            showHelp: true,
        })
    }

    doHideHelp() {
        this.setState({
            showHelp: false,
        })
    }

  render() {
    const { domainGroups } = this.props;
    const flattenedDomains = [];
    Object.keys(domainGroups).forEach(function(domainGroup){
      Object.keys(domainGroups[domainGroup]).forEach(function(domain){
        flattenedDomains.push(
          Object.assign(
            {
              id: domainGroup + ":" + domain,
              group: domainGroup,
              name: domain,
            },
            domainGroups[domainGroup][domain],
          )
        );
      });
    });

    const { domain, domainGroup, showHelp } = this.state;
    return (
      <div className="App">
        <div className={domain ? 'chosen-domain' : 'no-domain'}>
          <h1 className="no-domain__h1">Public Metadata Search

               { !showHelp && <button onClick={this.doShowHelp} className="helpButton appButton">HELP</button> }
               
          </h1>
           { showHelp && (
                    <div className="help-text">
                        <p>
                            This is a search engine of public files on the web.
                          </p>
                          <p>
                            Unlike most search engines that focus on webpages,
                            this focuses on other files like .PDF, .DOC, .JPEG, also known as source files.
                            </p>
                            <p>These types of source files often have metadata such as people's names, dates, GPS coordinates, redactions, and so on.</p>
                            <p>
                            Sites are included in the search index because they might host interesting files
                            (eg. <a href="https://www.theguardian.com/politics/2015/apr/27/small-business-owners-letter-telegraph-conservative-party">PR releases</a>)
                            but that doesn't mean the site is necessarily responsible for the file, they're just hosting it.
                        </p>
                        <button onClick={this.doHideHelp}  className="closeHelpButton appButton">Got it!</button>
                    </div>
                )}
          { !showHelp &&
          <div className="ddl_superSelect">
            <SuperSelect
              className="ddl_superSelect"
              placeholder="Categories" 
              searchPlaceholder="Search by site name"
              customOptionTemplateFunction={this.groceryCartItemTemplate}
              dataSource={flattenedDomains} 
              searchable={true}
              clearable={false}
              onChange={(item) => {
                this.setState({
                  domainGroup: item && item.group,
                  domain: item && item.domain
                })
              }}
              groupBy="group"
            />
          </div>
          }
        </div>
          
        {!showHelp && domain && <Site domain={domain} />}
      </div>
    );
  }

  groceryCartItemTemplate(item) {
    return(
    <div className="ddl_item" key={item.group + item.name}>
        <span className="ddl_domain">{item.name}</span>
        <span className="ddl_categories"> {
        categories
          .filter(function(category){
            return !!item[category]
          })
          .map(function(category){
            return <b key={category} className="ddl_category">{item[category]} {category}</b>;
          })
          
      }</span>
    </div>);
};
}

export default App;



