import React, { Component } from 'react'
import './site.css';
import Choice from './site.choice';
import JustData from './site.justdata'
import SiteGps from './site.gps'

export default class Site extends Component {
    constructor(props){
        super(props);
        this.state = {
            showHelp: false,
        };
        this.loadContent = this.loadContent.bind(this);
    }

    componentWillReceiveProps(newProps){
        const { domain } = newProps;
        this.loadContent(domain);
    }

    componentWillMount(){
        const { domain } = this.props;
        this.loadContent(domain);
    }

    loadContent(domain){
        fetch(`./${domain}.json`,  { credentials: 'include' })
            .then(response => response.json())
            .then((data) => {
                this.setState({ data })
            })
    }

    render(){
        const { domain } = this.props;
        const { data, showHelp } = this.state;
        if (!data) return (
            <p>loading...</p>
        );

        const formatGPSLabel = (label) => (
            label
                .split(',')
                .map(bit => (
                    parseFloat(bit).toFixed(2)
                ))
                .join(', ')
        )

        return (
            <div className="container">
                <h2>
                    {domain}
                </h2>
                <br />               
                <Choice title="People" key={`${domain}People`} Component={JustData} data={data.people} domain={domain} />
                <Choice title="GPS"  key={`${domain}GPS`} Component={SiteGps} data={data.gps} domain={domain} customFormatLabel={formatGPSLabel} />
                <Choice title="Date" key={`${domain}Date`} Component={JustData} data={data.date} domain={domain} />                
                <Choice title="Software" key={`${domain}Software`} Component={JustData} data={data.software} domain={domain} />        
                <Choice title="Redacted" key={`${domain}Redacted`} Component={JustData} data={data.redacted} domain={domain} />        
            </div>
        )
    }
}



