import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
//import registerServiceWorker from './registerServiceWorker';

fetch('./domains.json', { credentials: 'include' }).then(response => response.json()).then(domainGroups => {
    ReactDOM.render(<App domainGroups={domainGroups}/>, document.getElementById('root'));
    // registerServiceWorker();
});



