import React, { Component } from 'react'
import ReactModal from 'react-modal';
import './react-modal.css';
import getMeta from './get-meta';

const modalStyle = {
overlay : {
    position          : 'fixed',
    top               : 0,
    left              : 0,
    right             : 0,
    bottom            : 0,
    backgroundColor   : 'rgba(255, 255, 255, 0.75)'
  },
  content : {
    position                   : 'absolute',
    top                        : '40px',
    left                       : '40px',
    right                      : '40px',
    bottom                     : '40px',
    border                     : '1px solid #ccc',
    background                 : '#fff',
    overflow                   : 'auto',
    WebkitOverflowScrolling    : 'touch',
    borderRadius               : '4px',
    outline                    : 'none',
    padding                    : '20px'
  }
}

export default class JustData extends Component {
    constructor(props){
        super(props);
        this.state = {
            isOpen: false,
        };
        this.retrieveMetadata = this.retrieveMetadata.bind(this);
    }

    retrieveMetadata(domain, datum){
        this.setState({
            isOpen: false,
            metadata: undefined,
        });
        getMeta(domain, datum)
            .then(metadata => {
                this.setState({
                    isOpen: true,
                    metadata,
                })
            });
    }

    formatUrl(url){
        return (url.indexOf("@@") > -1) ? url.split("@@")[1] : url;
    }
  
    render(){
        const { domain, references } = this.props;

        const { isOpen, metadata } = this.state;
        
        return (
            <div>
                Metadata references: {references.map( (datum, index) => (
                    <button
                        key={index}
                        onClick={() => this.retrieveMetadata(domain, datum)}
                        title={JSON.stringify(datum)}
                    >
                        {index + 1}
                    </button>
                )) }

                <ReactModal
                    isOpen={isOpen}
                    onRequestClose={() => { this.setState({isOpen: false}); }}
                    contentLabel="Example Modal"
                    portalClassName="ReactModalPortal"
                    overlayClassName="ReactModal__Overlay"
                    bodyOpenClassName="ReactModal__Body--open"
                    ariaHideApp={true}
                    shouldCloseOnOverlayClick={true}
                    role="dialog"
                    >
                    { metadata && (
                        <div>
                            
                            <div>
                                <button className="appButton  appButton--cornerClose" onClick={() => { this.setState({isOpen: false}); }}>Close</button>
                                <h1>{metadata.filename}</h1>
                                <pre className="metadata-content">
                                    {JSON.stringify(metadata, null, 2)}
                                </pre>
                                <a href={this.formatUrl(metadata.url)} target="_blank" rel="noopener noreferrer nofollow" className="appButton appButton--link appButton--halfwide" title={`View ${metadata.filename}`}>
                                    View file on their site
                                </a>
                                <br /> <br /> 
                                <button className="appButton  appButton--halfwide close_Metadata" onClick={() => { this.setState({isOpen: false}); }}>Close</button>
                            </div>
                        </div>
                    )}
                </ReactModal>
            </div>
        )
    }
}



