import React, { Component } from 'react'
import Select from 'react-select';
import 'react-select/dist/react-select.css';
import './site.choice.css';

export default class SiteSoftware extends Component {
    constructor(props){
        super(props);
        this.state = {
            sort: 'alpha'
        };
        this.onChange = this.onChange.bind(this);
    }

    onChange(option){
        this.setState({
            chosenKey: option.value,
        });
    }
  
    render(){
        const { domain, data, Component, title, customFormatLabel } = this.props;
        
        const { chosenKey, sort } = this.state;

        const keys = data && Object.keys(data);

        const truncateLabel = (label) => (
            label.substr(0, 32)
        );

        let formatLabel = customFormatLabel || truncateLabel;

        if (keys) {
            switch(sort){
                case 'alpha':
                    keys.sort();
                    break;
                case 'hits':
                    keys.sort((a, b) => (
                        data[a].length < data[b].length
                    ));
                    break;
                default:
                    break;
            }
        }

        const sorts = [
            {id: 'alpha', title: 'Sort by alpha', label: 'aZ'},
            {id: 'hits', title: 'Sort by number of matches', label: '-/+'},
        ]
        return (
            <div>
                <h3>{title}</h3>
                {!data && '(none)'}
                {data && (
                    <span>
                        {sorts.map((sortItem, index) => (
                            <button
                                onClick={() => { this.setState({sort: sortItem.id}) }}
                                title={sortItem.title}
                                className={`sortBy ${sortItem.id === sort ? 'sortBy--selected' : ''}`}
                                key={index}
                            >
                                {sortItem.label}
                            </button>
                        ))}
                        <br />
                        <Select
                            onChange={this.onChange}
                            className="choice"
                            options={keys.map(key => (
                                { value: key, label: `${formatLabel(key)} (${data[key].length} matches)`}
                            ))}
                            placeholder={`Search ${title}`}
                        />
                    </span>
                )}
                {chosenKey && <Component references={data[chosenKey]} domain={domain} dataKey={chosenKey} />}
            </div>
        )
    }
}



