import React, { Component } from 'react'
import JustData from './site.justdata'

export default class SiteGps extends Component {
    constructor(props){
        super(props);
        this.state = {};
    }
  
    render(){
        const { domain, references, dataKey } = this.props;

        const keyParts = dataKey.split(',');
        const markerLat = keyParts[0];
        const markerLon = keyParts[1];

        const lat1 = markerLat * 0.99;
        const lon1 = markerLon * 0.99;
        const lat2 = markerLat * 1.01;
        const lon2 = markerLon * 1.01;
        
        return (
            <div>
               <iframe
                    style={{width:"100%"}}
                    height="500"
                    frameBorder="0"
                    scrolling="no"
                    marginHeight="0" 
                    marginWidth="0"
                    src={`https://www.openstreetmap.org/export/embed.html?bbox=${lon1}%2C${lat1}%2C${lon2}%2C${lat2}&layer=mapnik&marker=${markerLat}%2C${markerLon}`}
                />
                <JustData {...this.props} />
            </div>
        )
    }
}



